    company: "",
    manager: "",
    phone_num: "",
    email: "",
    develop_form: ["", ""],
    type: ["", "PC", "", ""],
    cost: ["500 ", "500 "],
    period: [],
    checkbox1: false,
  }),
  methods: {
    preview() {
      this.imagePreviews = [];
      for (let i = 0; i < this.images.length; i++) {
        let reader = new FileReader();
        reader.readAsDataURL(this.images[i]);
        reader.onloadend = () => {
          this.imagePreviews.push(reader.result);
        };
      }
    },
    toggle() {
      this.selectedClass = ".my-color";
    },
  },
};
</script>
